import React from "react";

import InternalLink from "components/reusable/atoms/links/InternalLink";
import brexLogo from "assets/img/logos/brex/logo-white.svg";

import { Header as StyledHeader, Logo } from "./elements";

const Header = ({ logo = brexLogo, linkToHome = false }) => (
  <StyledHeader>
    {linkToHome ? (
      <InternalLink to="/">
        <Logo alt="Brex Logo" src={logo} />
      </InternalLink>
    ) : (
      <Logo alt="Brex Logo" src={logo} />
    )}
  </StyledHeader>
);

export default Header;
